<template>
  <el-form
    :model="emailForm"
    status-icon
    ref="emailForm"
    label-width="100px"
    class="demo-emailForm"
  >
    <el-form-item label="邮箱:" prop="email">
      <el-input type="text" v-model="emailForm.email" placeholder="输入邮箱" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="图形验证码:" prop="email_code">
      <el-input type="password" v-model="emailForm.email_code" placeholder="验证码" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button
        class="form-button"
        type="primary"
        @click="submitForm('emailForm')"
      >确认</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      emailForm: {}
    };
  },
  watch: {},
  computed: {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.next();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    next() {
      this.$emit("next");
    }
  },
  created() {},
  mounted() {}
};
</script>


