<template>
  <div class="retrieve-wrapper">
    <div class="main-wrapper">
      <div class="close">
        <i class="el-icon-close" @click="open"></i>
      </div>
      <el-tabs v-model="activeName" :before-leave="moreState" :stretch="true">
        <el-tab-pane label="1.填写账号" name="1">
          <el-form
            :model="ruleForm"
            status-icon
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :class="{'item_el':state_switch=='2'}">
              <el-select v-model="state_switch" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <phone-form @next="next" v-show="state_switch==1" />
            <artificial-form @next="next" v-show="state_switch==2" />
            <email-form @next="next" v-show="state_switch==3" />
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="2.验证" name="2">
          <code-form @next="next" :phone="phone" />
        </el-tab-pane>
        <el-tab-pane label="3.重置密码" name="3">
          <password-form @next="next" :loadingState="loadingState" />
        </el-tab-pane>
        <el-tab-pane label="4.设置完成" name="4">
          <div class="success_wrapper">
            <div class="text-wrpper">
              <div class="icon-wrapper">
                <i class="el-icon-success i_success" v-if="success"></i>
                <i class="el-icon-error i_err" v-else></i>
              </div>
              <div class="text" v-if="success">您的密码已设置成功</div>
              <div class="text" v-else>很遗憾，密码重置失败</div>
            </div>
            <el-button type="primary" class="form-button" @click="removeLocalStorage">返回</el-button>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import {
  PhoneForm,
  ArtificialForm,
  EmailForm,
  CodeForm,
  PasswordForm,
} from "./components";
import { setEncryption } from "@/mixins/user";
import { resertPass } from "@/api/user";
export default {
  components: {
    PhoneForm,
    ArtificialForm,
    EmailForm,
    CodeForm,
    PasswordForm,
  },
  mixins: [setEncryption],
  data() {
    return {
      options: [
        {
          value: "1",
          label: "手机号找回",
        },
        // {
        //   value: "2",
        //   label: "人工找回",
        // },
        // {
        //   value: "3",
        //   label: "邮箱找回",
        // },
      ],
      phone: "",
      loadingState: false,
      state_switch: "1",
      success: false,
      ruleForm: {
        phone: "",
        phone_code: "",
        account: "",
        contact: "",
        Artificial_code: "",
        email: "",
        email_code: "",
      },
      activeName: localStorage.getItem("reset_index") || "1",
      state: false,
      rules: {},
    };
  },
  methods: {
    removeLocalStorage() {
      localStorage.removeItem("reset_index");
      localStorage.removeItem("reset_phone");
      localStorage.removeItem("reset_code");
      localStorage.removeItem("reset_password");
      this.$router.go(-1);
    },
    open() {
      this.$confirm("是否放弃此次操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeLocalStorage();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    moreState(tab, event) {
      return new Promise((resole, reject) => {
        if (this.state) {
          this.state = false;
          resole();
        }
        reject();
      });
    },
    next(props) {
      for (const key in props) {
        let object = JSON.stringify(props);
        localStorage.setItem(key, object);
        if (key === "reset_phone") {
          this.phone = props[key];
        }
      }
      let activeName = Number(this.activeName);
      if (activeName !== 3) {
        activeName++;
        this.activeName = activeName.toString();
        this.state = true;
      } else {
        this.loadingState = true;
        resertPass({
          phone_num: JSON.parse(localStorage.getItem("reset_phone"))
            .reset_phone,
          password: this.encryption(
            JSON.parse(localStorage.getItem("reset_password")).reset_password
          ),
          code: JSON.parse(localStorage.getItem("reset_code")).reset_code,
        }).then((res) => {
          console.log(res);
          if (res.status_code === 1) {
            this.success = true;
            this.loadingState = false;
            activeName++;
            this.activeName = activeName.toString();
            this.state = true;
          }
        });
      }
      if (activeName < 4) {
        localStorage.setItem("reset_index", this.activeName);
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.next();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    initIndex() {
      // let index = localStorage.getItem("reset_index");
      // let phone = localStorage.getItem("reset_phone");
      // let code = localStorage.getItem("reset_code");
      // if(phone&&code){
      // }
      // return;
    },
  },
  created() {
    this.$emit("customStyle", 0);
    // this.initIndex();
  },
};
</script>
<style lang="scss" scoped>
@import "../../../styles/global";
.retrieve-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  background: url("../../../../static/image/pc/login_bg.png") no-repeat;
  background-size: cover;
  .main-wrapper {
    position: relative;
    width: 750px;
    height: 450px;
    margin: 0 auto;
    // padding: 5px 30px 0;
    box-sizing: border-box;
    align-self: center;
    background-color: #ffffff;
    border-radius: 4px;
    overflow: hidden;
    //关闭按钮
    .close {
      z-index: 999;
      position: absolute;
      width: 100%;
      height: 65px;
      line-height: 65px;
      top: 0;
      right: 0;
      text-align: right;
      box-sizing: border-box;
      padding: 0 20px;
      i {
        font-size: 20px;
        font-weight: bold;
        color: #fff;
        cursor: pointer;
      }
    }
    /deep/ .el-form-item__content {
      //提交按钮
      .form-button {
        width: 100%;
        height: 45px;
        font-size: 18px;
        text-indent: 4px;
        letter-spacing: 4px;
        background-color: #73befb;
        border-color: #73befb;
        &:hover {
          opacity: 0.8;
        }
        &:active {
          opacity: 0.8;
        }
      }
    }
    /deep/ .el-tabs {
      .el-tabs__header.is-top {
        background-color: #73befb;
        padding: 0 50px;
        .el-tabs__nav {
          background-color: #73befb;
          .el-tabs__item {
            height: 65px;
            line-height: 65px;
            font-size: 18px;
            letter-spacing: 2px;
            color: #cee6fd;
          }
          .el-tabs__item:focus.is-active.is-focus:not(:active) {
            box-shadow: none;
            border-radius: 0;
          }
          //   .el-tabs__item:hover {
          //     color: #409eff;
          //     cursor: pointer;
          //   }
          .el-tabs__item.is-active {
            color: #ffffff;
          }
          .el-tabs__active-bar {
            background-color: #4c84c1;
          }
        }
      }
    }
    /deep/ .demo-codeForm {
      width: 350px;
      margin: 30px auto 0;
      .el-form-item__content {
        .divider-wrapper {
          display: flex;
          align-items: center;
          color: #666666;
          .divider {
            display: inline-block;
            width: 4px;
            height: 16px;
            border-radius: 2px;
            background-color: #eac385;
            margin-right: 5px;
          }
        }
        .item-wrapper {
          width: 100%;
          display: flex;
          justify-content: space-between;
          .el-button {
            font-size: 16px;
            padding: 8px 40px;
          }
          .el-input-wrapper {
            width: calc(100% - 150px);
          }
        }
        .send-button {
          background-color: #eeeeee;
          border-radius: 4px;
          width: 120px;
          height: 40px;
          margin: 0;
          padding: 0;
          border: 1px solid transparent; //自定义边框
          outline: none;
          color: #666666;
          overflow: hidden;
          cursor: pointer;
          @include center;
          &:hover {
            opacity: 0.8;
          }
          &:active {
            opacity: 1;
          }
        }
        .block {
          > span {
            display: block;
            width: 100%;
            height: 100%;
            color: #999999;
          }
        }
        .send-button_s {
          background-color: #eeeeee;
          border-radius: 4px;
          width: 120px;
          height: 40px;
          margin: 0;
          padding: 0;
          border: 1px solid transparent; //自定义边框
          outline: none;
          color: #666666;
          overflow: hidden;
          @include center;
        }
      }
    }
    .demo-ruleForm {
      padding-left: 105px;
      width: 450px;
      .item_el {
        margin: 0;
      }
      .el-select {
        width: 350px;
      }
    }
    .demo-passwordForm {
      width: 450px;
      padding: 50px 0 30px 105px;
      /deep/ .show-pwd {
        position: absolute;
        right: 10px;
        top: 0;
        font-size: 16px;
        color: rgba(51, 51, 51, 0.5);
        cursor: pointer;
        user-select: none;
      }
    }
    .success_wrapper {
      width: 300px;
      height: auto;
      margin: 100px auto 0;
      position: relative;
      .text-wrpper {
        @include center;
        .icon-wrapper {
          margin-right: 20px;
          i {
            font-size: 30px;
          }
          .i_success {
            color: #e5b467;
          }
          .i_err {
            color: #f35858;
          }
        }
        .text {
          font-size: 20px;
          padding-left: 10px;
        }
      }
      .form-button {
        margin-top: 80px;
        width: 100%;
        height: 45px;
        font-size: 18px;
        text-indent: 4px;
        letter-spacing: 4px;
        background-color: #73befb;
        border-color: #73befb;
        &:hover {
          opacity: 0.8;
        }
        &:active {
          opacity: 0.8;
        }
      }
    }
  }
}
</style>


