<template>
  <el-form
    :model="passwordForm"
    :rules="Rules"
    label-width="100px"
    ref="passwordForm"
    class="demo-passwordForm"
  >
    <el-form-item label="密码：" prop="pass">
      <el-input
        ref="pass"
        :type="pwdType"
        v-model="passwordForm.pass"
        placeholder="请设定登录密码"
        autocomplete="off"
      ></el-input>
      <span class="show-pwd" @click="showPwd">
        <svg-icon icon-class="eye" />
      </span>
    </el-form-item>
    <el-form-item style="margin-bottom:35px" label="确认密码：" prop="checkPass">
      <el-input
        ref="checkPass"
        :type="checkPassType"
        v-model="passwordForm.checkPass"
        placeholder="请再输入一次登录密码"
        autocomplete="off"
      ></el-input>
      <span class="show-pwd" @click="showcheckPass">
        <svg-icon icon-class="eye" />
      </span>
    </el-form-item>
    <el-form-item>
      <el-button
        class="form-button"
        type="primary"
        :loading="loadingState"
        @click="updatePassword"
      >下一步</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { isPassWord } from "@/utils/validate";
export default {
  components: {},
  props: {
    loadingState: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (!isPassWord(value)) {
        callback(new Error("请输入至少6位"));
      } else {
        if (this.passwordForm.checkPass !== "") {
          this.$refs.passwordForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (!isPassWord(value)) {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.passwordForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      pwdType: "password",
      checkPassType: "password",
      passwordForm: {
        pass: "",
        checkPass: "",
      },
      Rules: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    updatePassword() {
      this.$refs["passwordForm"].validate((valid) => {
        if (valid) {
          this.next();
        }
      });
    },
    next() {
      this.$emit("next", { reset_password: this.passwordForm.pass });
    },
    showPwd() {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
      this.$nextTick(() => {
        this.$refs.pass.focus();
      });
    },
    showcheckPass() {
      if (this.checkPassType === "password") {
        this.checkPassType = "";
      } else {
        this.checkPassType = "password";
      }
      this.$nextTick(() => {
        this.$refs.checkPass.focus();
      });
    },
  },
  created() {},
  mounted() {},
};
</script>


