<template>
  <el-form
    :model="phoneForm"
    :rules="Rules"
    status-icon
    ref="phoneForm"
    label-width="100px"
    class="demo-phoneForm"
  >
    <el-form-item label="注册手机号:" prop="phone">
      <el-input type="text" v-model="phoneForm.phone" placeholder="输入注册手机号" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="图形验证码:" prop="sidentify" class="sidentify-wrapper">
      <div class="item-wrapper" style="width: 100%;display: flex;justify-content: space-between;">
        <div class="el-input-wrapper" style="width: calc(100% - 150px);">
          <el-input placeholder="请输入图形验证码" v-model="phoneForm.sidentify"></el-input>
        </div>
        <div
          class="login-code"
          @click="refreshCode"
          style="width:120px;height:40px;border: 1px solid #d7dae2;border-radius: 4px;overflow: hidden;box-sizing:border-box;"
        >
          <el-image style="width:100%;height:100%;" :src="refresh.b64s" lazy></el-image>
        </div>
      </div>
    </el-form-item>
    <el-form-item>
      <el-button class="form-button" type="primary" @click="submitForm('phoneForm')">下一步</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { artificial_code } from "@/mixins/user";
import { isPhone } from "@/utils/validate";
import { captcha } from "@/api/code";
export default {
  name: "phoneForm",
  props: {},
  data() {
    const validatePhone = (rule, value, callback) => {
      if (!isPhone(value)) {
        callback(new Error("手机号不符合规则"));
      } else {
        callback();
      }
    };
    const validateSidentify = (rule, value, callback) => {
      if (!value) return callback(new Error("图形验证码不能为空"));
      this.valideCode(value, callback);
    };
    return {
      phoneForm: {
        sidentify: "",
        phone: "",
      },
      refresh: {
        b64s: "",
        id: "",
      },
      Rules: {
        phone: [{ trigger: "blur", validator: validatePhone }],
        sidentify: [{ trigger: "blur", validator: validateSidentify }],
      },
    };
  },
  watch: {},
  mixins: [artificial_code],
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.next();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    valideCode(value, callback) {
      captcha(
        JSON.stringify({ id: this.refresh.id, value: this.phoneForm.sidentify })
      ).then((response) => {
        if (!response.data.verify_status) {
          this.refreshCode();
          return callback(new Error("图形验证码错误"));
        }
        callback();
      });
    },
    next() {
      this.$emit("next", { reset_phone: this.phoneForm.phone });
    },
  },
  created() {},
  mounted() {},
};
</script>


