<template>
  <el-form
    :model="artificialForm"
    status-icon
    ref="artificialForm"
    label-width="100px"
    class="demo-artificialForm"
  >
    <el-form-item style="margin:0">
      <span style="font-size:13px;letter-spacing: 0px;color: #eac385;">请填写以下信息，稍后我们会联系您</span>
    </el-form-item>
    <el-form-item label="账号:" prop="account">
      <el-input
        type="text"
        v-model="artificialForm.account"
        placeholder="输入手机号/邮箱/用户编号/用户名"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <el-form-item label="联系方式:" prop="contact">
      <el-input
        type="password"
        v-model="artificialForm.contact"
        placeholder="输入您的联系方式"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <el-form-item label="图形验证码:" prop="artificial_code">
      <el-input
        type="password"
        v-model="artificialForm.artificial_code"
        placeholder="验证码"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <el-form-item>
      <el-button
        class="form-button"
        type="primary"
        @click="submitForm('artificialForm')"
      >提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "ArtificialForm",
  props: {},
  data() {
    return {
      artificialForm: {}
    };
  },
  watch: {},
  computed: {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.next();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    next() {
      this.$emit("next");
    }
  },
  created() {},
  mounted() {}
};
</script>


