<template>
  <el-form :model="loginForm" status-icon ref="codeForm" :rules="Rules" class="demo-codeForm">
    <el-form-item style="margin-bottom:10px">
      <div class="divider-wrapper">
        <div class="divider"></div>
        <span>请填写收到的短信验证码</span>
      </div>
    </el-form-item>
    <el-form-item prop="code" class="smsCode-wrapper">
      <div class="item-wrapper">
        <div class="el-input-wrapper">
          <!-- <el-input
                      placeholder="请输入短信验证码"
                      v-model="loginForm.smsCode"
                      @keyup.enter.native="submitForm('loginForm')"
          ></el-input>-->
          <el-input
            placeholder="请输入短信验证码"
            v-model="loginForm.code"
            @keyup.enter.native="submitForm('loginForm',$event)"
          ></el-input>
        </div>
        <button type="button" :class="!show?'send-button_s block':'send-button block'">
          <span v-show="show" @click="sendText">发送验证码</span>
          <span v-show="!show" class="count">{{count}}s后重新发送</span>
        </button>
      </div>
    </el-form-item>
    <!-- <el-form-item style="margin-bottom:50px">
      <el-row :gutter="20">
        <el-col :span="16">
          <el-input v-model="codeForm.code" placeholder="请输入短信验证码" type="text"></el-input>
        </el-col>
        <el-col :span="8">
          <button type="button" class="code-col">发送验证码</button>
        </el-col>
      </el-row>
    </el-form-item>-->
    <el-form-item>
      <el-button class="form-button" type="primary" @click="validationPhone('codeForm')">下一步</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { isSmsCode } from "@/utils/validate";
import { getCode } from "@/mixins/user";
export default {
  components: {},
  mixins: [getCode],
  props: {
    phone: {
      type: String,
      default: "",
    },
  },
  data() {
    const validateCode = (rule, value, callback) => {
      if (!isSmsCode(value)) {
        callback(new Error("验证码不符合规则"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        code: "",
        phone: "",
      },
      Rules: {
        code: [{ required: true, trigger: "blur", validator: validateCode }],
      },
    };
  },
  watch: {
    phone(newval, oldval) {
      this.loginForm.phone = newval;
    },
  },
  methods: {
    validationPhone(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.next();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    next() {
      this.$emit("next", { reset_code: this.loginForm.code });
    },
  },
  created() {},
  mounted() {},
};
</script>


